___CSS_LOADER_EXPORT___.push([module.id, `.table-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.table {
    display: table;
    border-collapse: collapse;
}

.table-row {
    display: table-row;
}

.table-cell {
    display: table-cell;
    padding: 10px;
    border: 1px solid black;
}

.article-item {
    margin-bottom: 20px;
}

.form-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.edit-article {
    height: 200vh;
    width: 100%;
    max-width: 400px;
    padding: 20px;
    border-radius: 8px;
}

.edit-profile {
    height: 100vh;
    width: 100%;
    max-width: 400px;
    padding: 20px;
    border-radius: 8px;
}

.admin {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 40vh;
}


/* .edit-wrapper {
    min-width: 433px;
} */

/* .regist-wrapper {
    min-width: 433px;
} */

.form-wrapper {
    max-width: 433px;
    margin: 0 30px;
    width: 100%;
    padding: 20px;
    border: none;
    border-radius: 5px;
    box-shadow: 0 4px 14px hsl(355deg 25% 15% / 14.5%);
}

