.form-outline {
    display: flex;
    flex-direction: column;
}

form h3 {
    font-size: 32px;
    font-weight: 500;
    line-height: 42px;
    text-align: center;
}

label {
    display: block;
    margin-top: 30px;
    font-size: 16px;
    font-weight: 500;
}

input {
    display: block;
    height: 50px;
    width: 100%;
    border-radius: 3px;
    padding: 0 10px;
    margin-top: 8px;
    font-size: 14px;
    font-weight: 300;
    background-color: #ffff;
}

.form-profile .bx {
    display: inline-block;
    font-size: 30px;
    margin: auto 0;
}

.form-profile {
    display: flex;
}

.input_profile {
    display: block;
    height: 50px;
    width: 100%;
    border-radius: 0;
    padding: 0 10px;
    margin-top: 8px;
    font-size: 14px;
    font-weight: 300;
    border-style: none;
    border-bottom-style: outset;
}

textarea {
    display: block;
    width: 100%;
    resize: none;
    padding: 15px;
    border-radius: 3px;
    font-size: 14px;
    font-weight: 300;
}

.btn-acc {
    margin-top: 50px;
    width: 100%;
    background-color: var(--container-color);
    color: var(--second-color);
    padding: 15px 0;
    font-size: 18px;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
}

.btn-acc:hover {
    background-color: #414ec5;
}

#UpdateProfile {
    margin-top: 16px;
}

.btn-del {
    border: none;
    background-color: rgb(197, 6, 6);
    color: var(--second-color);
    padding: 0 15px;
    font-size: 18px;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
}

.btn-del:hover {
    background-color: rgb(255, 0, 0);
}

.btn-edit {
    border: none;
    background-color: rgb(221, 155, 32);
    color: var(--second-color);
    padding: 0 15px;
    font-size: 18px;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
}

.btn-edit:hover {
    background-color: rgb(250, 162, 0);
}

.admin-nav {
    width: 100%;
    height: 80px;
    background: var(--container-color);
